<template>
    <div>
        <div class="modelclass">
            <div>
                <!-- <div class="modesclass">Cookie：<input class="inputclass" type="text" v-model="headers.Cookie"></div> -->
                <div>Token：<input class="inputclass" type="text" v-model="headers.Token"></div>
                <div>条数：<input class="inputclass" type="text" v-model="querdata.limit"></div>

            </div>

            <button @click="makeRequest" class="buttonvq">发送请求</button>
            总数据：{{ dataD.result.totalCount }} 本次数据：{{ dataD.result.list.length }}
            <button @click="automationRequests" class="buttonvq">自动化保单的数据({{ tooal }})</button>
            <button @click="exportToExcel" class="buttonvq">生成Excel</button>


        </div>
        <div @click="removeCharAt">fdf</div>

        <!-- -->

        <div style="margin-left: 20px;"> 数据列表</div>
        <div class="itemclass">

            <div>
                <div v-for="(item, index) in dataD.result.list" :key="index" class="margintop">
                    <div> 姓名：{{ item.customerName }}<div> 性別：{{ item.sex }}</div>
                        <div> 年龄：{{ item.dayberi }}</div>
                    </div>


                    <div> 地址：{{ item.address }}</div>
                    <div> 客户号：{{ item.custNo }} <div> 星级：{{ item.vipFlag }}</div>
                        <div> 总保费：{{ item.prem }}</div>
                    </div>


                    <div> 保单名称：{{ item.customerName }}<div> 客户类型：{{ item.sourceTypeName }}</div>
                    </div>

                    <div> 分配时间：{{ item.allocTime }}<div> 投保时间：{{ item.maxInForceDate }}</div>
                        <div> 手机号码：{{ item.mobile }}</div>
                    </div>



                </div>
            </div>
            <div class="jsonclass">
                <div v-for="(item, index) in dataD.result.list" :key="index" class="margintop">
                    {{ item }},

                </div>
            </div>
        </div>


    </div>
</template>

<script>
import axios from 'axios';
import FileSaver from 'file-saver';
import XLSX from 'xlsx';
export default {
    data() {
        return {
            url: '/api/gdsfp-web-uat/app/listSzCustomersPage',
            headers: {
                'Accept': 'application/json, text/plain, */*',
                'Content-Type': 'application/json',
                // 'Cookie': 'Path=/; acw_tc=ac11000117228670656125799e00760484b2fb68168bafbe5ae999655b8ad5; capital-token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzUxMiJ9.eyJzdWIiOiI0NDAxMDAwMDI2OTM5MCIsImlhdCI6MTcyMjg2NzA2NywiZXhwIjoxNzIzNDcxODY3fQ.RvMJBMoojqx25kgyZn43fBZTat9VVnOhMmuFP9dppJ-f9yVhENXKD5h6SCVT-QFyWHH9VKuhXQH_HUlISJ_5kw',
                // eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzUxMiJ9.eyJzdWIiOiI0NDAxMDAwMDI2OTM5MCIsImlhdCI6MTcyMjg2NzA2NywiZXhwIjoxNzIzNDcxODY3fQ.RvMJBMoojqx25kgyZn43fBZTat9VVnOhMmuFP9dppJ-f9yVhENXKD5h6SCVT-QFyWHH9VKuhXQH_HUlISJ_5kw
                'Token': ''
            },
            querdata: {
                fuzzyQueryInput: "",
                hasVisitFlag: -1,
                limit: 300,
                maxAge: "",
                maxPremium: "",
                minAge: "",
                minPremium: "",
                month: "",
                page: 1,
                sex: "",
                sourceType: "0",
                vipFlag: ""
            },
            tooal: 0,
            hasMoreData: true,
            dataD: {
                result: {
                    totalCount: 0,
                    list: []
                }
            },
            isend: false
        }
    },
    methods: {
        async makeRequest() {
            let that = this;
            // if (that.isend || !that.hasMoreData) return
            await axios.post(that.url, that.querdata, {
                headers: that.headers
            })
                .then(response => {
                    // if(response.data.status!=200)  return
                    that.dataD.result.totalCount = response.data.result.totalCount;
                    that.dataD.result.list = response.data.result.list
                    // that.querdata.page = response.data.result.list.length == 0 ? that.querdata.page : ++that.querdata.page;
                    that.hasMoreData = response.data.result.list.length === that.querdata.limit


                })
                .catch(error => {
                    // alert('请输入正确的Cookie和Token')
                    console.log(error, 12345)
                });
        },
        async salesq(code) {
            let that = this
            let salesNo = await axios.get('/api/gdsfp-web-uat/app/szCustomerInfoNew?idCode=' + code + '&sourceType=0', { headers: that.headers })

            return salesNo
        },
        removeCharAt(code) {
            let s = code;
            let startIndex = 6;

            s = s.slice(0, startIndex) + s.slice(startIndex + 2)
            s = s.slice(0, -1);
            return s

        },
        async automationRequests() {
            this.isend = true
            let that = this
            for (const item of that.dataD.result.list) {
                try {
                    let salesNo = await that.salesq(item.idCode)
                    const response = await axios.get('/api/gdsfp-web-uat/app/queryCntrList?idCode=' + item.idCode + '&salesNo=' + salesNo.data.result.salesno, { headers: that.headers });

                    // console.log(response.data, 'kjj')
                    if (response.data.result.length != 0) {
                        let polCodeStr = '';
                        for (let items of response.data.result) {
                            for (let subItem of items.list) {
                                polCodeStr += subItem.polCode + '¥'+subItem.premium+',';
                            }
                        }

                        // 去掉最后一个多余的逗号
                        item.allocName = polCodeStr.slice(0, -1);
                    } else {
                        const response2 = await axios.get('/api/gdsfp-web-uat/app/queryCntrList?idCode=' + that.removeCharAt(item.idCode) + '&salesNo=' + salesNo.data.result.salesno, { headers: that.headers });
                        let polCodeStr = '';
                        for (let items of response2.data.result) {
                            for (let subItem of items.list) {
                                polCodeStr += subItem.polCode +  '¥'+subItem.premium+',';
                            }
                        }

                        // 去掉最后一个多余的逗号
                        item.allocName = polCodeStr.slice(0, -1);
                       
                    }

                    that.tooal = Number(that.tooal) + 1

                } catch (error) {
                    // console.error(`请求 ID 为 ${item.id} 时出错: `, error);
                }
            }
            if (that.tooal == that.dataD.result.totalCount) {
                alert('生成保单的数据成功')
            }

        },
        dayberi(birthDate) {
            const birth = new Date(birthDate);
            const current = new Date();
            let age = current.getFullYear() - birth.getFullYear();
            const monthDiff = current.getMonth() - birth.getMonth();
            if (monthDiff < 0 || (monthDiff === 0 && current.getDate() < birth.getDate())) {
                age--;
            }
            return age;
        },
        exportToExcel() {
            // 自定义表头
            const headers = ['姓名', '身份证', '性別', '年龄', '地址', '客户号', '星级', '总保费', '客户类型', '分配时间', '投保时间', '保单名称', '手机号码'];
            // 数据列表

            let newData = []
            let data1 = this.dataD.result.list
            for (let i = 0; data1.length > i; i++) {
                newData.push({
                    '姓名': data1[i].customerName,
                    '身份证': data1[i].idCode,
                    '性別': data1[i].sex,
                    '年龄': this.dayberi(data1[i].birthDate),
                    '地址': data1[i].address,
                    "客户号": data1[i].custNo,
                    '星级': data1[i].vipFlag,
                    '总保费': data1[i].prem,
                    '客户类型': data1[i].sourceTypeName,
                    '分配时间': data1[i].allocTime,
                    '投保时间': data1[i].maxInForceDate,
                    '保单名称': data1[i].allocName,
                    '手机号码': data1[i].mobile


                })
            }
            if (data1.length != newData.length) {
                return
            }

            const worksheet = XLSX.utils.json_to_sheet(newData, { header: headers });
            const workbook = XLSX.utils.book_new();
            XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');

            const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
            const dataBlob = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });

            FileSaver.saveAs(dataBlob, '表格信息.xlsx');
        },

    }
}
</script>
